<template>
    <div>
        <div style="margin: 10px">
            <label>业务: </label>
            <el-select v-model="biz_value" placeholder="请选择业务" style="width: 400px">
                <el-option
                    v-for="item in biz_list"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
        </div>
        <div style="margin: 10px">
            <label>IP: </label>
            <el-input class="float-left" style="width: 600px;height: 40px" v-model="ip_input" placeholder="请输入IP"></el-input>
            <el-button type="primary" style="float: right;" @click="searchHost">查询主机</el-button>
        </div>
        <div style="margin: 10px">
            <el-table :data="hostInfo" height="450" border style="width: 100%">
                <el-table-column prop="bk_host_innerip" label="内网IP"></el-table-column>
                <el-table-column prop="bk_os_name" label="系统名"></el-table-column>
                <el-table-column prop="bk_host_name" label="主机名"></el-table-column>
                <el-table-column prop="bk_cloud_name" label="云区域"></el-table-column>
                <el-table-column prop="sex" label="Mem（%）"></el-table-column>
                <el-table-column prop="sex" label="Disk（%）"></el-table-column>
                <el-table-column prop="sex" label="CPU（%）"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <div style="align-items: center">
                            <i class="el-icon-search" @click="searchInfo(scope.row)"></i>
                            <i class="el-icon-view" @click="addMonitor(scope.row)"></i>
                            <i class="el-icon-delete" @click="deleteMonitor(scope.row)"></i>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'examination',
        data() {
            return {
                ip_input: '',
                biz_list: [],
                biz_value: '',
                hostInfo: [],
                tableSize: 100,
                monitor: false
            }
        },
        created() {
            this.searchBiz()
        },
        methods: {
            searchHost() {
                let params = {};
                params['bk_biz_id'] = this.biz_value;
                params['bk_host_innerip'] = this.ip_input;
                this.loading = true;
                this.$http.post('search_host', params).then(res => {
                    this.loading = false;
                    if (res['result']) {
                        this.hostInfo = res.data
                    } else {
                        this.$message.error('查询主机失败' + res['bk_error_msg'])
                    }
                })
            },
            searchBiz() {
                this.$http.post('search_biz', {}).then(res => {
                    this.biz_list = res.data
                })
            },
            searchInfo(item) {
                this.$http.post('search_info', item).then(res => {
                    if (res['result']) {
                        this.hostInfo = this.hostInfo.concat(res.data)
                    } else {
                        this.$message.error('查询主机使用率信息失败')
                    }
                })
            },
            deleteMonitor(item) {
                let params = {};
                params['bk_host_id'] = item['bk_host_id'];
                this.$http.post('delete_monitor', params).then(res => {
                    if (res['result']) {
                        this.monitor = false;
                        this.$message.success('删除监控成功')
                    } else {
                        this.$message.error('删除监控失败')
                    }
                })
            },
            addMonitor(item) {
                this.$http.post('add_monitor', item).then(res => {
                    if (res['result']) {
                        this.monitor = true;
                        this.$message.success('添加监控成功')
                    } else {
                        this.$message.error('添加监控失败')
                    }
                })
            },
        }
    }
</script>

<style scoped>

</style>



